*,
a {
  padding: 0;
  margin: 0;
  text-decoration: none;
}
body {
  background-color: var(---body-bg);
}
:root {
  ---body-bg: #fcfcfc;
  ---cor-texto: #ffffff;
  ---cor-textobk: #252525;
  ---body-color: #a6a6a6;

  ---link-color: #88db00;
  ---link-color-hover: #046425;

  ---destaque: #046425;
  ---destaque-hover: #88db00;

  ---nav-bk: #b9bf0f;

  ---bannerPrincipal: url("../img/banner5.jpg");
}



:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #e83e8c;
    --red: #dc3545;
    --orange: #fd7e14;
    --yellow: #ffc107;
    --green: #28a745;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --light: #f8f9fa;
    --dark: #343a40;
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  }